import React from 'react';
import { connect } from 'react-redux';
import './AppHeader.css';
import classnames from 'classnames';
import { app, ROUTE_STATES } from '../../app';
import * as screenfull from "screenfull";
import {Screenfull} from "screenfull";
import { IMapStateToProps } from '../../redux/connect';

// https://github.com/sindresorhus/screenfull.js/issues/126#issuecomment-488796645
let sf = screenfull as Screenfull;


interface IAppHeaderProps {
    className: string;
    route?: ROUTE_STATES;
    isFullScreen?: boolean;
}


const mapStateToProps: IMapStateToProps<IAppHeaderProps, 'isFullScreen'> = (state) => ({
    route: state.routing.route,
    isFullScreen: state.fullScreen.inFullscreen
});


class AppHeaderComponent extends React.Component<IAppHeaderProps> {

    toggleState = () => app.toggleFullscreen();


    render() {
        const { className, isFullScreen, route } = this.props;
        const mainRouteState = route === ROUTE_STATES.MAIN;
        const conditionalClasses = {
            'AppHeader_small': !mainRouteState
        };

        return (
            <div className={classnames('AppHeader', className, conditionalClasses)}>
                { !isFullScreen && mainRouteState && (
                    <button type="button" onClick={() => this.toggleFullScreen()}
                        className="AppHeader__fullscreen"
                    >
                        Развернуть
                    </button>
                )}
                { !mainRouteState && (
                    <button type="button" onClick={() => app.goToRoute(app.ROUTE_STATES.MAIN)}
                        className="AppHeader__to-main"
                    >
                        На главную
                    </button>
                )}
            </div>
        );
    }


    componentDidMount() {
        sf.on('change', this.toggleState);
    }


    componentWillUnmount() {
        sf.off('change', this.toggleState);
    }


    toggleFullScreen() {
        sf.toggle()
            .catch(error => console.log(
                `while toggling fullsreen state, current state is ${this.props.isFullScreen}`,
                error
            ));
    }

}


export const AppHeader = connect(mapStateToProps)(AppHeaderComponent);
