import { createReducer, IReducerObj } from '../create-reducer';
import { createDoActions } from '../create-do-actions';
import { IFullScreenState, fullScreenInitialState } from './full-screen-state';


export interface IFullScreenActionsToPayloads {
    TOGGLE: void
}


export const fullScreenActions = createDoActions<IFullScreenActionsToPayloads>('TOGGLE');


const reducerObj: IReducerObj<IFullScreenState, IFullScreenActionsToPayloads> = {
    'TOGGLE': (state) => ({
        inFullscreen: !state!.inFullscreen
    }),
};


export const fullScreenReducer = createReducer(reducerObj, fullScreenInitialState);
