import { IQuestionsList } from '../../models/qa';
import { storage } from '../../utils/storage';
import defaultQuestionsJson from './default-questions.json';

const defaultQuestions = defaultQuestionsJson.questions as IQuestionsList;


export interface IQuestionsState {
    questions: IQuestionsList;
    currentQuestionId: number;
};


export const questionsInitialState: IQuestionsState = {
    questions: storage.questions.restore(defaultQuestions) as IQuestionsList,
    currentQuestionId: 0,
};
