import { Unsubscribe } from 'redux';
import { IAppStore } from '../redux/store';
import { IAppState } from '../redux/state';
import { doActions } from '../redux/do-actions';
import { ROUTE_STATES } from './route-states';
import { IQuestion, IQuestionsList, IAnswer } from '../models/qa';
import { IExportedQuestions } from '../models/export';
import { storage } from '../utils/storage';
import { IUser } from '../models/user';
import md5 from 'blueimp-md5';

export class App {

    private _store: IAppStore | null = null;

    private storeUnsubscriber: Unsubscribe | null = null;

    private storedQuestions: IQuestionsList = [];
    private storedUsers: IUser[] = [];

    private get state(): IAppState {
        return this._store!.getState();
    }


    ROUTE_STATES = ROUTE_STATES;


    setStore(store: IAppStore) {
        this._store = store;
        this.unsubscribeStore();
        if (store) {
            store.subscribe(() => {
                const state = store.getState();
                this.storeQuestions(state);
                this.storeUsers(state);
            });
        }
    }


    restartGame() {
        doActions.users.CLEAR_REG_USER(this._store);
        doActions.users.CLEAR_CATEGORIES(this._store);
        doActions.questions.GO_TO_QUESTION(this._store, 0);
        doActions.routing.GO_TO(this._store, ROUTE_STATES.QUEST);
    }


    toggleFullscreen() {
        doActions.fullScreen.TOGGLE(this._store);
    }


    goToRoute(route: ROUTE_STATES) {
        doActions.routing.GO_TO(this._store, route);
    }


    addQuestion(question: IQuestion) {
        doActions.questions.ADD(this._store, question);
    }


    deleteQuestion(question: IQuestion) {
        doActions.questions.DELETE(this._store, question);
    }


    updateQuestion(question: IQuestion) {
        doActions.questions.UPDATE(this._store, question);
    }


    nextQuestion(answer: IAnswer, questionId: number) {
        doActions.users.HANDLE_CATEGORIES(this._store, answer.categories);
        doActions.questions.GO_TO_QUESTION(this._store, questionId);
        const registrationUser = this.state.users.registrationUser;
        if (registrationUser) {
            doActions.users.ADD_OR_UPDATE(this._store, {
                ...registrationUser,
                lastQuestionId: questionId
            });
        }

        console.log('categories', this.state.users.currentCategories);
    }

    updateQuestions(questions: IQuestionsList | IExportedQuestions) {
        const qs = questions instanceof Array ? questions : (questions as IExportedQuestions).questions;
        doActions.questions.REPLACE(this._store, qs);
    }


    registerUser(user: IUser) {
        doActions.users.REG_USER(this._store, user);
    }


    goToAdmin() {
        const storedAdminPassHash = storage.adminPass.restore();
        const msg = storedAdminPassHash
            ? 'Введите пароль для доступа к разделу "Администрирование"'
            : 'Задайте новый пароль для доступа к разделу "Администрирование"';
        const adminPass = prompt(msg, '');

        if (!adminPass) {
            alert('Вы не ввели пароль!');
            return;
        }

        const adminPassHash = md5(adminPass);

        if (storedAdminPassHash) {
            if (storedAdminPassHash !== adminPassHash) {
                alert('Пароль введён неверно!');
                return;
            }
        } else {
            storage.adminPass.store(adminPassHash);
            alert('Пароль сохранён!');
        }

        this.goToRoute(ROUTE_STATES.ADMIN_MAIN);
    }


    private unsubscribeStore() {
        if (this.storeUnsubscriber) {
            this.storeUnsubscriber();
            this.storeUnsubscriber = null;
        }
    }


    private storeQuestions(state: IAppState) {
        const stateQuestions = state.questions.questions;
        if (stateQuestions !== this.storedQuestions) {
            this.storedQuestions = stateQuestions;
            storage.questions.store(stateQuestions);
        }
    }


    private storeUsers(state: IAppState) {
        const stateUsers = state.users.users;
        if (stateUsers !== this.storedUsers) {
            this.storedUsers = stateUsers;
            storage.users.store(stateUsers);
        }
    }

}
