import { Reducer } from 'redux'
import { IAction } from './store';


// { actionName: (state, action) => state }
export type IReducerObj<TState, TActionsToPayloads> = {
    [K in keyof TActionsToPayloads]: Reducer<TState, IAction<TActionsToPayloads[K]>>
}


export function createReducer<TActionsToPayload, TState>
        (reducerObj: IReducerObj<TState, TActionsToPayload>, initialState: TState)
{
    return (state: TState | undefined, action: IAction<any>) => {
        if (!state) {
            return initialState;
        }

        const actionType: keyof TActionsToPayload = action.type as keyof TActionsToPayload;
        const reducer = (reducerObj as any)[actionType] as Reducer<TState, IAction<any>>;
        if (reducer) {
            return reducer(state!, action) as TState;
        }
        return state!;
    };
}
