import React from 'react';
import { connect } from 'react-redux';
import { IMapStateToProps } from '../../redux/connect';
import './Quest.css';
import { app } from '../../app';
import { IQuestion, IQuestionsList, IAnswer } from '../../models/qa';
import classnames from 'classnames';
import { placeholders, handlePlaceholders } from '../../utils/placeholders';
import { RegistrationForm } from '../RegistrationForm/RegistrationForm';
import { IUser } from '../../models/user';
import { handleTextCategories } from '../../utils/handle-text-categories';


interface IQuestProp {
    questions?: IQuestionsList;
    currentQuestionId?: number;
    registrationUser?: IUser | null;
    currentCategories?: IUser['categories'];
}


interface IQuestState {
    userInput: string;
    currentQuestionId: number;
}


const mapStateToProps: IMapStateToProps<IQuestProp, 'questions' | 'currentQuestionId'> = (state) => ({
    questions: state.questions.questions,
    currentQuestionId: state.questions.currentQuestionId,
    registrationUser: state.users.registrationUser,
    currentCategories: state.users.currentCategories,
});


class QuestComponent extends React.Component<IQuestProp, IQuestState> {

    state: IQuestState = {
        userInput: '',
        currentQuestionId: 0
    };


    static getDerivedStateFromProps(props: IQuestProp, state: IQuestState): IQuestState | null {
        if (props.currentQuestionId && props.currentQuestionId !== state.currentQuestionId) {
            return { ...state, userInput: '', currentQuestionId: props.currentQuestionId };
        }
        return null;
    }


    render() {

        const currentQuestionId = this.props.currentQuestionId!;
        const questions = this.props.questions!;
        const regUser = this.props.registrationUser;

        let currentQuestion: IQuestion|undefined|null;
        if (!currentQuestionId) {
            currentQuestion = questions!.filter(question => question ? question.isFirst : false)[0];
        } else {
            currentQuestion = questions!.filter(question => question ? question.id === currentQuestionId : false)[0];
        }

        if (!currentQuestion) {
            return (
                <h1>Первый вопрос не выбран или следующий вопрос "{currentQuestionId}" не найден!</h1>
            );
        }

        const isFinal = currentQuestion.answers.filter(Boolean).length === 0;
        const hasReg = currentQuestion.text.includes(placeholders.reg);
        const hideAnswers = hasReg && (!regUser || !regUser.phone
            || (!regUser.firstName && !regUser.lastName) || !regUser.login);

        return (
            <div>
                {/* {currentQuestion.title && (
                    <div className="Quest__title">
                        {currentQuestion.title}
                    </div>
                )} */}
                <div className={classnames('Quest__text', 'Quest__ml')}>
                    { this.renderQuestionText(currentQuestion) }
                </div>
                {!isFinal && !hideAnswers && (
                    <ul className="Quest__answers">
                        {
                            currentQuestion.answers.map(answer => {
                                if (!answer || !answer.text || !answer.nextQuestion) {
                                    return null;
                                }
                                const renderedAnswer = this.renderAnswer(answer);
                                if (!renderedAnswer) {
                                    return null;
                                }

                                return (
                                    <li key={answer.id} className="Quest__action-container">
                                        { renderedAnswer }
                                    </li>
                                );
                            })
                        }
                    </ul>
                )}
                {isFinal && (
                    <ul className="Quest__final-actions">
                        <li>
                            <button type="button"
                                onClick={() => app.restartGame()}
                            >
                                Попробовать снова
                            </button>
                        </li>
                        <li>
                            <button type="button"
                                onClick={() => app.goToRoute(app.ROUTE_STATES.MAIN)}
                            >
                                Дать поиграть другим
                            </button>
                        </li>
                    </ul>
                )}
            </div>
        );
    }


    private renderAnswer(answer: IAnswer): React.ReactElement | null {
        if (answer.needUserInput) {
            return (
                <form onSubmit={() => this.handleSelectedAnswer(answer)}>
                    <div className="Quest__user-input-container">
                        <input type="text"
                            maxLength={100}
                            value={this.state.userInput}
                            onChange={(event) => this.setState({ userInput: event.target.value }) }
                            className="std"
                            placeholder="Введите ответ здесь"
                        />
                        { this.state.userInput && (
                            <button type="submit"
                                className="Quest__user-input-submit"
                            >
                                Отправить ответ
                            </button>
                        )}
                    </div>
                </form>
            );
        }

        let text = answer.text;
        if (answer.randomLine) {
            const texts = text.split('\n');
            const randomTextIndex = Math.floor(Math.random() * texts.length);
            text = texts[randomTextIndex];
        }

        const handledText = this.handleQAText(text);

        if (!handledText) {
            return null;
        }

        return (
            <button type="button" onClick={() => this.handleSelectedAnswer(answer)}
                className={classnames('Quest__action', 'Quest__ml')}
            >
                { handledText }
            </button>
        );
    }


    private handleSelectedAnswer(answer: IAnswer) {
        let nextQuestion: number | undefined = answer.nextQuestion;

        if (answer.needUserInput) {
            const expectedValue = answer.text.toLowerCase().trim();
            const inputValue = this.state.userInput.toLowerCase().trim();
            nextQuestion = inputValue === expectedValue
                ? answer.nextQuestion
                : answer.incorrectUserInputNextQuestion;
        }

        app.nextQuestion(answer, nextQuestion || 0);
    }


    private renderQuestionText(question: IQuestion): React.ReactElement | string | Array<React.ReactElement | string> {
        if (question.randomLineText) {
            const texts = question.text.split('\n').filter(Boolean);
            const randomTextIndex = Math.floor(Math.random() * texts.length);
            const text = texts[randomTextIndex];
            return this.handleQAText(text);
        }

        if (question.text.includes(placeholders.reg)) {
            const textParts = question.text.split(placeholders.reg);
            return [
                this.handleQAText(textParts[0].trim()),
                <RegistrationForm key={1} className="Quest__registration"/>,
                this.handleQAText(textParts[1].trim()),
            ];
        }

        return this.handleQAText(question.text);
    }


    private handleQAText(text: string): string {
        const afterPlaceholders = handlePlaceholders(text, this.props.registrationUser);
        const afterCategories = handleTextCategories(afterPlaceholders, this.props.currentCategories);
        return afterCategories;
    }

};


export const Quest = connect(mapStateToProps)(QuestComponent);
