/**
 * Дополняет заданными символами слева до нужной длины строки.
 * @see https://developer.mozilla.org/ru/docs/Web/JavaScript/Reference/Global_Objects/String/padStart
 */
export function padStart(item: string|number, strLength: number, symbol: string): string {
    const strItem = '' + item;
    return strItem.padStart(strLength, symbol);
}
