import React from 'react';
import { connect } from 'react-redux';
import { IMapStateToProps } from '../../redux/connect';
import './QuestionsManagement.css';
import { app } from '../../app';
import { QuestionEdit } from '../QuestionEdit/QuestionEdit';
import { IQuestionsList } from '../../models/qa';
import { placeholdersInfo, IPlaceholders } from '../../utils/placeholders';


interface IQuestionsManagementProps {
    questions?: IQuestionsList;
}


const mapStateToProps: IMapStateToProps<IQuestionsManagementProps, 'questions'> = (state) => ({
    questions: state.questions.questions
});


const QuestionsManagementComponent: React.FC<IQuestionsManagementProps> = (props) => {
    const qs = props.questions!;

    return (
        <div>
            <div>
                Задайте вопросы квеста!
                <br />
                Вопросы без ответов считаются финальными состояними квеста!
                <br />
                Ответы без текста или без указания цели перехода отображены не будут!
                <br />
                При удалении вопросов или ответов номера остальных вопросов/ответов не меняются!
                <br />
                Фильтрация по категориям: формат "cat:" + ИМЯ_КАТЕГОРИИ + ":" + ЗНАЧЕНИЕ + УСЛОВИЕ + ":"
                Например: "cat:ios:3|неплохо знаете iOS" - строка "неплохо знаете iOS" будет показана,
                если у пользователя точно 3 ответа категории ios. "cat:ios:0|cat:android:1{'<'}|человек-android"
                - строка "человек-android" будет показана, если у пользователя нет ответов категории ios
                и больше 1 ответа категории "android". В качестве условия можно использовать '{'<'}' или '{'>'}'.
            </div>
            <div>
                В тексте вопросов и ответов можно использовать подстановки:
                {
                    Object.keys(placeholdersInfo).map((placeholder, index) => (
                        <div key={index}>
                            { placeholder } - { placeholdersInfo[placeholder as IPlaceholders] }
                        </div>
                    ))
                }
            </div>

            <QuestionEdit
                className="QuestionsManagement__question"
                onSave={(question) => app.addQuestion(question)}
                qs={qs}
            />

            {
                qs.map(question => question && (
                    <QuestionEdit
                        className="QuestionsManagement__question"
                        question={question}
                        key={question.id}
                        onSave={(question) => app.updateQuestion(question)}
                        onDelete={(question) => app.deleteQuestion(question)}
                        qs={qs}
                    />
                ))
            }

            <div className="QuestionsManagement__link-container">
                <button type="button"
                    onClick={() => app.goToRoute(app.ROUTE_STATES.MAIN)}
                >
                    На главную
                </button>
            </div>

        </div>
    );

};


export const QuestionsManagement = connect(mapStateToProps)(QuestionsManagementComponent);
