import React, { ChangeEvent } from 'react';
import './FileImport.css';
import { readFileAsString } from '../../utils/file';


interface IFileImportProps {
    buttonText: string;
    onContentRead: (content: any) => void;
    parseJson?: boolean;
}


interface IFileImportState {
}


class FileImportComponent extends React.Component<IFileImportProps, IFileImportState> {

    state: IFileImportState = {
    };

    render() {
        const { buttonText } = this.props;
        return (
            <label className="FileImport">
                { buttonText }
                <input type="file"
                    className="FileImport_import"
                    onChange={(event) => this.onFileSelected(event)}
                />
            </label>
        );
    }


    private onFileSelected(event: ChangeEvent<HTMLInputElement>) {
        const files = event.target.files;
        const file = files && files[0];
        if (file) {
            readFileAsString(file)
                .then(content => {
                    const { parseJson, onContentRead } = this.props;

                    if (!parseJson) {
                        return onContentRead(content);
                    }

                    const json = JSON.parse(content);
                    onContentRead(json);
                })
                .catch(error => console.error(`while reading file ${file.name}`, error));
        }
    }

};


export const FileImport = FileImportComponent;
