export function extendState<T>(state: T, toAdd: Partial<T>): T;
export function extendState<T, K extends keyof T>(state: T, key: K, value: T[K]): T

export function extendState<T, K extends keyof T>(state: T, toAddOrKey: Partial<T> | K, value?: T[K]): T {
    const toAdd = arguments.length === 3
        ? ({ [toAddOrKey as K]: value } as any) as Partial<T>
        : toAddOrKey as Partial<T>;
    return {
        ...state,
        ...toAdd
    };
}
