import { createReducer, IReducerObj } from '../create-reducer';
import { createDoActions } from '../create-do-actions';
import { IRoutingState, routeInitialState } from './routing-state';
import { ROUTE_STATES } from '../../app/route-states';


export interface IRoutingActionsToPayloads {
    GO_TO: ROUTE_STATES;
}


export const routingActions = createDoActions<IRoutingActionsToPayloads>('GO_TO');


const reducerObj: IReducerObj<IRoutingState, IRoutingActionsToPayloads> = {
    'GO_TO': (state, action) => ({
        route: action.payload
    }),
};


export const routingReducer = createReducer(reducerObj, routeInitialState);
