import React from 'react';
import './FileExport.css';
import { IFileDescription, releaseFileObject, getFileObject, getFileName, FILE_TYPES } from '../../utils/file';


interface IFileExportProps {
    getFileDescription: () => IFileDescription;
    buttonText: string;
}


interface IFileExportState {
    fileObjectUrl: string;
    fileName: string;
}


class FileExportComponent extends React.Component<IFileExportProps, IFileExportState> {

    state: IFileExportState = {
        fileObjectUrl: '',
        fileName: ''
    };

    render() {
        const { buttonText } = this.props;
        const { fileObjectUrl, fileName } = this.state;

        return (
            <div>
                <button type="button"
                    onClick={() => this.prepareFile()}
                    className="FileExport__button"
                >
                    { buttonText }
                </button>
                { fileObjectUrl && (
                    <a href={fileObjectUrl} download={fileName}
                        className="FileExport__button"
                    >
                        Скачать файл
                    </a>
                )}
            </div>
        );
    }


    private prepareFile() {
        this.releaseStateFileObject();

        const fileDescription = this.props.getFileDescription();
        this.setState({
            fileObjectUrl: getFileObject({
                content: this.stringifyContent(fileDescription.content),
                fileType: fileDescription.type
            }),
            fileName: getFileName(fileDescription.nameSection, FILE_TYPES[fileDescription.type].toLowerCase())
        });
    }


    private releaseStateFileObject() {
        const { fileObjectUrl } = this.state;
        if (fileObjectUrl) {
            releaseFileObject(fileObjectUrl);
        }
    }


    private stringifyContent(content: any): string {
        if (!content) {
            return '';
        }
        if (typeof content === 'object') {
            // readable json
            return JSON.stringify(content, null, 4);
        }
        return JSON.stringify(content);
    }

};


export const FileExport = FileExportComponent;
