import { createReducer, IReducerObj } from '../create-reducer';
import { createDoActions } from '../create-do-actions';
import { IUsersState, usersInitialState } from './users-state';
import { IUser } from '../../models/user';
import { extendState } from '../../utils/extend-state';


export interface IUsersActionsToPayloads {
    REG_USER: IUser;
    ADD_OR_UPDATE: IUser;
    CLEAR_REG_USER: void;
    CLEAR_CATEGORIES: void;
    HANDLE_CATEGORIES: string | undefined;
}


export const usersActions = createDoActions<IUsersActionsToPayloads>(
    'ADD_OR_UPDATE', 'REG_USER', 'CLEAR_REG_USER', 'HANDLE_CATEGORIES', 'CLEAR_CATEGORIES'
);


const reducerObj: IReducerObj<IUsersState, IUsersActionsToPayloads> = {
    'HANDLE_CATEGORIES': (state, action) => {
        if (!action.payload) {
            return state!;
        }
        const currentCategories = { ...state!.currentCategories };
        action.payload
            .split(',')
            .map(category => category.trim())
            .filter(Boolean)
            .forEach(category => currentCategories[category] = (currentCategories[category] || 0) + 1);
        return extendState(state!, 'currentCategories', currentCategories);
    },
    'CLEAR_CATEGORIES': (state) => extendState(state!, 'currentCategories', {}),
    'CLEAR_REG_USER': (state) => extendState(state!, 'registrationUser', null),
    'REG_USER': (state, action) => extendState(state!, 'registrationUser', action.payload),
    'ADD_OR_UPDATE': (state, action) => {
        const rawUser = action.payload;
        const newUser = Object.keys(rawUser).reduce<IUser>((memo, key) => {
            const userKey = key as keyof IUser;
            const newValue = rawUser[userKey];
            memo[userKey] = typeof newValue === 'string' ? newValue.trim() : newValue;
            return memo;
        }, {} as IUser);
        newUser.categories = { ...state!.currentCategories };
        const sameUserIndex = state!.users.findIndex(user => user.phone === newUser.phone);
        const hasSameUser = sameUserIndex !== -1;
        let newUsersList = [...state!.users];
        if (hasSameUser) {
            newUsersList[sameUserIndex] = newUser;
        } else {
            newUsersList.push(newUser);
        }
        extendState(state!, 'currentUser', newUser);
        return extendState(state!, 'users', newUsersList);
    },
};


export const usersReducer = createReducer(reducerObj, usersInitialState);
