import { padStart } from './text';

export enum FILE_TYPES {
    JSON = 'JSON',
    CSV = 'CSV',
}


const mimeTypes: { [key in FILE_TYPES]: string } = {
    JSON: 'application/json',
    CSV: 'text/csv',
};


export interface IFileDescription {
    content: any;
    nameSection: string; // имя в файле без timestamp и расширения
    type: FILE_TYPES;
}


/**
 * Выгружает пользователю файл.
 * @see http://jsfiddle.net/UselessCode/qm5AG/
 * @param {object} params
 * @param {string} params.content
 * @param {string} params.type MIME-type.
 * @param {string} [params.charset='utf-8']
 * @param {object} [params.fileObject] Ранее сгенерированный объект для выгрузки этого файла.
 * @returns {File|Blob} See URL.createObjectURL API.
 */
export function getFileObject(params: {
    content: string,
    fileType: FILE_TYPES,
    charset?: string,
    fileObjectUrl?: string
}): string {
  releaseFileObject(params.fileObjectUrl);

  const charset = params.charset || 'utf-8';
  const mimeType = mimeTypes[params.fileType];
  const type = `data:${mimeType};charset=${charset}`;
  const data = new Blob([params.content], { type });
  const fileObject = window.URL.createObjectURL(data);
  return fileObject;
}

/**
 * @see http://jsfiddle.net/UselessCode/qm5AG/
 */
export function releaseFileObject(fileObjectUrl?: string) {
  if (fileObjectUrl) {
    // If we are replacing a previously generated file we need to
    // manually revoke the object URL to avoid memory leaks.
    window.URL.revokeObjectURL(fileObjectUrl);
  }
}


