/**
 * Читает с диска файл.
 * @param {File} file Объект File, который может быть получен из input/type=file.
 * @returns {Promise<string>}
 */
export function readFileAsString(file: File): Promise<string> {
    return new Promise<string>((resolve, reject) => {
        const reader = new FileReader();

        reader.onload = (event: ProgressEvent) => {
            const content = (event.target as any).result;
            resolve(content);
        };

        reader.onerror = (error) => {
            reject(error);
        };

        reader.readAsText(file);
    });
}


/**
 * Получение timestamp для использования в именах файлов, например.
 * @returns {string}
 */
function getTimestamp() {
    const now = new Date();
    const y = now.getFullYear();
    const m = padStart(now.getMonth() + 1, 2, '0');
    const d = padStart(now.getDate(), 2, '0');
    const H = padStart(now.getHours(), 2, '0');
    const M = padStart(now.getMinutes(), 2, '0');
    const S = padStart(now.getSeconds(), 2, '0');
    return `${y}-${m}-${d}--${H}-${M}-${S}`;
}


export function getFileName(section: string, extension?: string): string {
    const timestamp = getTimestamp();
    const fileName = extension
        ? `${section}-${timestamp}.${extension}`
        : `${timestamp}-${section}`;
    return fileName;
}
