import React from 'react';
import { connect } from 'react-redux';
import './App.css';
import { AppHeader } from '../AppHeader/AppHeader';
import { IMapStateToProps } from '../../redux/connect';
import { Routing } from './../Routing/Routing';
import { ROUTE_STATES } from '../../app';
import classnames from 'classnames';


interface IAppProps {
    route?: ROUTE_STATES,
}


const mapStateToProps: IMapStateToProps<IAppProps, 'route'> = (state) => ({
    route: state.routing.route,
});


const AppComponent: React.FC<IAppProps> = ({ route }) => {
    const conditionalClasses = {
        '_header_small': route !== ROUTE_STATES.MAIN,
        '_quest-in-progress': route === ROUTE_STATES.QUEST,
    };

    return (
        <div className={classnames('App', conditionalClasses)}>
            <div className="App__container">
                <AppHeader className="App__header"/>
                <Routing/>
            </div>
        </div>
    );
};


export const App = connect(mapStateToProps)(AppComponent);
