import React from 'react';
import { connect } from 'react-redux';
import { IMapStateToProps } from '../../redux/connect';
import { app } from '../../app';
import { IUser } from '../../models/user';
import './RegistrationForm.css';
import { getNewUser } from '../../utils/get-new-user';


interface IRegistrationFormProps {
    route: string;
    className?: string;
}


interface IRegistrationFormState {
    user: IUser;
}


const mapStateToProps: IMapStateToProps<IRegistrationFormProps, 'route'> = (state) => ({
    route: state.routing.route
});


class RegistrationFormComponent extends React.Component<IRegistrationFormProps, IRegistrationFormState> {

    state = {
        user: getNewUser()
    };

    render() {
        const mainId = `RegistrationForm`;
        const firstNameId = `${mainId}_fn`;
        const lastNameId = `${mainId}_ln`;
        const phoneId = `${mainId}_phone`;
        const loginId = `${mainId}_login`;

        return (
            <ul className={this.props.className}>
                <li>
                    <label htmlFor={firstNameId} className="RegistrationForm__label">
                        Имя:
                    </label>
                    <input type="text"
                        className="std"
                        id={firstNameId}
                        name={firstNameId}
                        maxLength={100}
                        value={this.state.user.firstName}
                        onChange={(event) => this.updateUser('firstName', event)}
                    />
                </li>
                <li>
                    <label htmlFor={lastNameId} className="RegistrationForm__label">
                        Фамилия:
                    </label>
                    <input type="text"
                        className="std"
                        id={lastNameId}
                        name={lastNameId}
                        maxLength={100}
                        value={this.state.user.lastName}
                        onChange={(event) => this.updateUser('lastName', event)}
                    />
                </li>
                <li>
                    <label htmlFor={phoneId} className="RegistrationForm__label">
                        Телефон:
                    </label>
                    <input type="text"
                        className="std"
                        id={phoneId}
                        name={phoneId}
                        maxLength={100}
                        value={this.state.user.phone}
                        onChange={(event) => this.updateUser('phone', event)}
                    />
                </li>
                <li>
                    <label htmlFor={loginId} className="RegistrationForm__label">
                        Логин:
                    </label>
                    <input type="text"
                        className="std"
                        id={loginId}
                        name={loginId}
                        maxLength={100}
                        value={this.state.user.login}
                        onChange={(event) => this.updateUser('login', event)}
                    />
                </li>
            </ul>
        );
    }


    private updateUser(userProp: keyof IUser, event: React.ChangeEvent<HTMLInputElement>) {
        const value = event.target.value;
        const user = { ...this.state.user, [userProp]: value };
        this.setState({ user });
        app.registerUser(user);
    }

}


export const RegistrationForm = connect(mapStateToProps)(RegistrationFormComponent);
