import React from 'react';
import { connect } from 'react-redux';
import { IMapStateToProps } from '../../redux/connect';
import { app, ROUTE_STATES } from '../../app';
import { Landing } from '../Landing/Landing';
import { AdminMenu } from '../AdminMenu/AdminMenu';
import { QuestionsManagement } from '../QuestionsManagement/QuestionsManagement';
import { Quest } from '../Quest/Quest';


interface IRoutingProps {
    route?: ROUTE_STATES
}


const mapStateToProps: IMapStateToProps<IRoutingProps, 'route'> = (state) => ({
    route: state.routing.route
});


const RoutingComponent: React.FC<IRoutingProps> = ({ route }) => {
    switch (route) {
        case app.ROUTE_STATES.QUEST:
            return <Quest/>
        case app.ROUTE_STATES.ADMIN_QUESTIONS_LIST:
            return <QuestionsManagement/>;
        case app.ROUTE_STATES.ADMIN_MAIN:
            return <AdminMenu/>;
        case app.ROUTE_STATES.MAIN:
        default:
            return <Landing/>;
    }
};


export const Routing = connect(mapStateToProps)(RoutingComponent);
