import { createReducer, IReducerObj } from '../create-reducer';
import { createDoActions } from '../create-do-actions';
import { IQuestionsState, questionsInitialState } from './questions-state';
import { IQuestion, INewQuestion, IQuestionsList } from '../../models/qa';
import { extendState } from '../../utils/extend-state';


export interface IQuestionsActionsToPayloads {
    ADD: INewQuestion,
    DELETE: IQuestion,
    UPDATE: IQuestion,
    REPLACE: IQuestionsList,
    GO_TO_QUESTION: number,
}


export const questionsActions = createDoActions<IQuestionsActionsToPayloads>(
    'ADD', 'DELETE', 'UPDATE', 'REPLACE', 'GO_TO_QUESTION',
);


const reducerObj: IReducerObj<IQuestionsState, IQuestionsActionsToPayloads> = {
    'ADD': (state, action) => {
        // INewQuestion will become IQuestion after 'id' will be added.
        const question = { ...action.payload } as IQuestion;
        const questions = state!.questions;
        question.id = questions.length + 1;
        return extendState(state!, 'questions', [...questions, question]);
    },
    'DELETE': (state, action) => {
        const questionToDelete = action.payload;
        const questions = state!.questions.map(question =>
            question && question.id !== questionToDelete.id ? question : null);
        return extendState(state!, 'questions', questions);
    },
    'UPDATE': (state, action) => {
        const questionToUpdate = action.payload;
        const resetIsFirst = questionToUpdate.isFirst;
        const questions = state!.questions.map(question => {
            if (!question) {
                return null;
            }
            if (resetIsFirst) {
                question = { ...question, isFirst: false };
            }
            return question.id === questionToUpdate.id ? questionToUpdate : question;
        });
        return extendState(state!, 'questions', questions);
    },
    'GO_TO_QUESTION': (state, action) => {
        return extendState(state!, 'currentQuestionId', action.payload);
    },
    'REPLACE': (state, action) => {
        return extendState(state!, 'questions', action.payload);
    },
};


export const questionsReducer = createReducer(reducerObj, questionsInitialState);
