import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import { App } from './components/App/App';
import { Provider } from 'react-redux'
import { configureStore, IAppStore } from './redux/store';
import { app } from './app';

const store = configureStore() as IAppStore;

app.setStore(store);

const appElement = (
    <Provider store={store}>
        <App/>
    </Provider>
);

document.title = 'OK Mobius 2019';

ReactDOM.render(appElement, document.getElementById('root'));
