import { IUser } from '../models/user';


function replaceCategory(line: string, categories: IUser['categories']): string | null {
    if (!line.startsWith('cat:')) {
        return line;
    }
    const matches = line.match(/^cat:(\w+):(\d+)([<>]?)/);
    if (!matches || matches.length !== 4) {
        console.log('handleTextCategories: match was failed for string', line);
        return line;
    }
    const matchStr = matches[0];
    const catName = matches[1];
    const catValue = parseInt(matches[2], 10);
    const condition = matches[3];
    if (isNaN(catValue)) {
        console.log('handleTextCategories: category value isNaN:', matches[2]);
        return line;
    }
    const currentCatValue = categories[catName] || 0;
    // Category condition + separator (|)
    const restLine = line.slice(matchStr.length + 1);
    const checkPassed = (!condition && catValue === currentCatValue)
        || (condition === '<' && catValue < currentCatValue)
        || (condition === '>' && catValue > currentCatValue);
    if (checkPassed) {
        return replaceCategory(restLine, categories);
    }
    return null;
}


export function handleTextCategories(text: string, categories?: IUser['categories']): string {
    if (!categories) {
        return text;
    }
    const handledText = text.split('\n')
        .map(line => replaceCategory(line, categories!))
        .filter(line => line !== null)
        .join('\n');
    return handledText;
}
