import { IAppStore } from './store';


type IDoActionsWithPayload<TActionsToPayload> = {
    [K in keyof TActionsToPayload]: (store: IAppStore | null, payload: TActionsToPayload[K]) => void;
}


export function createDoActions<TActionsToPayload>(...actionsNamesList: Array<keyof TActionsToPayload>): IDoActionsWithPayload<TActionsToPayload> {
    return actionsNamesList.reduce((memo, actionName) => {
        memo[actionName] = (store, payload) => {
            if (!store) {
                return;
            }

            store.dispatch({
                type: actionName as string,
                payload
            });
        };
        return memo;
    }, {} as IDoActionsWithPayload<TActionsToPayload>);
}
