import React from 'react';
import { connect } from 'react-redux';
import './AdminMenu.css';
import { app } from '../../app';
import { IQuestionsList } from '../../models/qa';
import { IExportedQuestions } from '../../models/export';
import { IMapStateToProps } from '../../redux/connect';
import { FileExport } from '../FileExport/FileExport';
import { FileImport } from '../FileImport/FileImport';
import { FILE_TYPES } from '../../utils/file';
import { IUser } from '../../models/user';


interface IAdminMenuProps {
    questions?: IQuestionsList;
    users?: IUser[];
}


const mapStateToProps: IMapStateToProps<IAdminMenuProps, 'questions'> = (state) => ({
    questions: state.questions.questions,
    users: state.users.users,
});


const AdminMenuComponent: React.FC<IAdminMenuProps> = ({ questions, users }) => {
    return (
        <ul className="AdminMenu__menu">
            <li>
                <button type="button" onClick={() => app.goToRoute(app.ROUTE_STATES.ADMIN_QUESTIONS_LIST)}>
                    Управление вопросами
                </button>
            </li>
            <li>
                <FileImport buttonText="Прочитать вопросы из файла"
                    parseJson={true}
                    onContentRead={(questions: IQuestionsList | IExportedQuestions) => {
                        app.updateQuestions(questions);
                        window.alert('Вопрос прочитаны успешно!');
                    }}
                />
            </li>
            <li>
                <FileExport buttonText="Скачать вопросы"
                    getFileDescription={() => ({
                        type: FILE_TYPES.JSON,
                        content: { questions },
                        nameSection: 'questions'
                    })}
                />
            </li>
            <li>
                <FileExport buttonText="Выгрузить пользователей"
                    getFileDescription={() => ({
                        type: FILE_TYPES.JSON,
                        content: { users },
                        nameSection: 'users'
                    })}
                />
            </li>
            <li>
                <button type="button" onClick={() => app.goToRoute(app.ROUTE_STATES.MAIN)}>
                    На главную
                </button>
            </li>
        </ul>
    );
};


export const AdminMenu = connect(mapStateToProps)(AdminMenuComponent);
