import * as React from 'react';
import { IQuestion, IAnswer, IQuestionsList } from '../../models/qa';
import './QuestionEdit.css';
import classnames from 'classnames';
import { cloneQuestion } from '../../utils/clone-question';
import { placeholders } from '../../utils/placeholders';


interface IQuestionEditProps {
    question?: IQuestion;
    qs: IQuestionsList;
    onSave: (question: IQuestion) => void;
    onDelete?: (question: IQuestion) => void;
    className?: string;
}


interface IQuestionEditState {
    question?: IQuestion;
    isNew: boolean;
    hideTextError: boolean;
}


function getNewQuestion(): IQuestion {
    return {
        id: 0,
        text: '',
        // title: '',
        answers: []
    }
}


export class QuestionEdit extends React.Component<IQuestionEditProps, IQuestionEditState> {

    state: IQuestionEditState = {
        isNew: true,
        hideTextError: false,
    };


    static getDerivedStateFromProps(props: IQuestionEditProps, state: IQuestionEditState): IQuestionEditState | null {
        const propQuestion = props.question;
        const stateQuestion = state.question;

        if (propQuestion) {
            if (stateQuestion && stateQuestion.id === propQuestion.id) {
                // update isFirst
                return {
                isNew: false,
                hideTextError: false,
                question: {
                    ...stateQuestion,
                    isFirst: propQuestion.isFirst
                }
                }
            } else {
                return {
                isNew: false,
                hideTextError: false,
                question: cloneQuestion(propQuestion)
                };
            }
        } else if (stateQuestion) {
            return null;
        }

        return {
            isNew: true,
            hideTextError: true,
            question: getNewQuestion()
        };
    }


    render() {
        const { question, isNew, hideTextError } = this.state;
        const { qs, className } = this.props;
        const hasAnswers = question!.answers.filter(Boolean).length > 0;
        const isFinal = !hasAnswers;
        const tooManyReg = question!.text.split(placeholders.reg).length > 2;

        const mainHtmlId = `QuestionEdit__${question!.id}`;
        // const titleHtmlId = `${mainHtmlId}__title`;
        const textHtmlId = `${mainHtmlId}__text`;
        const isFirstHtmlId = `${mainHtmlId}__isFirst`;
        const randomTextHtmlId = `${mainHtmlId}__randomText`;

        const rootId = this.getRootId(this.props.question ? this.props.question.id : 0);

        return (
            <div className={classnames('QuestionEdit', className)} id={rootId}>
                <form onSubmit={(event) => this.onSubmit(question!, event)}>
                    <div className={classnames('QuestionEdit__indented', 'QuestionEdit__row')}>
                        <b>
                            {isNew
                                ? 'Новый вопрос'
                                : isFinal
                                    ? `Финал, #${question!.id}`
                                    : `Вопрос, #${question!.id}`
                            }
                        </b>
                        {!isFinal && (
                            <div className={classnames('QuestionEdit__wideItem', 'QuestionEdit__checkboxContainer')}>
                                <label>
                                    <input type="checkbox"
                                        name={isFirstHtmlId}
                                        checked={!!question!.isFirst}
                                        onChange={(event) => this.updateQuestionProp('isFirst', event.target.checked)}
                                        className="QuestionEdit__checkbox"
                                    />
                                    первый вопрос
                                </label>
                            </div>
                        )}
                    </div>
                    {/* <div className={classnames('QuestionEdit__row', 'QuestionEdit__indented')}>
                        <label htmlFor={titleHtmlId}>
                            Заголовок
                        </label>
                        <input name={titleHtmlId}
                            type="text"
                            id={titleHtmlId}
                            value={question!.title || ''}
                            onChange={(event) => this.updateQuestionProp('title', event.target.value)}
                            className="QuestionEdit__wideItem"
                            placeholder="Необязательно"
                        />
                    </div> */}
                    <div className={classnames('QuestionEdit__row', 'QuestionEdit__indented')}>
                        <label htmlFor={textHtmlId}>
                            Текст
                        </label>
                        <div className="QuestionEdit__wideItem">
                            <textarea name={textHtmlId}
                                id={textHtmlId}
                                className={classnames('', 'QuestionEdit__bigEditor')}
                                value={question!.text || ''}
                                onChange={(event) => this.updateQuestionProp('text', event.target.value)}
                                placeholder="Заполните текст вопроса!"
                            ></textarea>
                            <div className="QuestionEdit__checkboxContainer">
                                <label>
                                    <input type="checkbox"
                                        name={randomTextHtmlId}
                                        checked={!!question!.randomLineText}
                                        onChange={(event) => this.updateQuestionProp('randomLineText', event.target.checked)}
                                        className="QuestionEdit__checkbox"
                                    />
                                    показывать рандомную строку
                                </label>
                            </div>
                            { question!.randomLineText && (
                                <div>
                                    ВНИМАНИЕ: пользователю будет отображаться рандомная непустая строка из текста этого вопроса!!!
                                </div>
                            )}
                            { tooManyReg && (
                                <div className="QuestionEdit__error">
                                    Вы вставили больше одного блока ввода регистрационных данных!
                                </div>
                            )}
                        </div>
                    </div>

                    {
                        !question!.text && !hideTextError && (
                            <div className={classnames('QuestionEdit__indented', 'QuestionEdit__error')}>
                                Заполните текст вопроса!
                            </div>
                        )
                    }

                    {
                        hasAnswers && (
                            <div className={'QuestionEdit__indented'}>
                                Ответы:
                            </div>
                        )
                    }

                    {
                        question!.answers.map((answerOrNull, index) => {
                            if (!answerOrNull) {
                                return null;
                            }

                            const answer = answerOrNull!;
                            const answerHtmlId = `${mainHtmlId}_answer_${answer.id}`;
                            const answerGotoHtmlId = `${answerHtmlId}_goto`;
                            const answerInputHtmlId = `${answerHtmlId}_input`;
                            const answerWrongInputGotoHtmlId = `${answerHtmlId}_wrongInputGot`;
                            const answerRandomLineHtmlId = `${answerHtmlId}_randomLine`;
                            const answerCategoriesHtmlId = `${answerHtmlId}_categories`;

                            return (
                                <div className={'QuestionEdit__indented'} key={answer.id}>
                                    <div className={classnames('QuestionEdit__row', 'QuestionEdit__indented')} key={`${answer.id}_text`}>
                                        <textarea
                                            name={answerHtmlId}
                                            id={answerHtmlId}
                                            value={answer.text || ''}
                                            onChange={(event) => this.updateAnswerProp(answer, 'text', event.target.value)}
                                            className={classnames('QuestionEdit__wideItem', 'QuestionEdit__bigEditor')}
                                            placeholder="Заполните текст ответа!!!"
                                        ></textarea>
                                        <button type="button" onClick={() => this.deleteAnswer(index)}>
                                            Удалить
                                        </button>
                                    </div>
                                    {
                                        !answer.text && (
                                            <div className={classnames('QuestionEdit__indented', 'QuestionEdit__error')}>
                                                Заполните текст ответа!!!
                                            </div>
                                        )
                                    }
                                    <div className={classnames('QuestionEdit__row', 'QuestionEdit__indented', 'QuestionEdit__hor-indented')}>
                                        <label htmlFor={answerCategoriesHtmlId}>
                                            Категории:
                                        </label>
                                        <input type="text"
                                            value={answer.categories || ''}
                                            onChange={(event) => this.updateAnswerProp(answer, 'categories', event.target.value)}
                                            className="QuestionEdit__wideItem"
                                        />
                                    </div>
                                    <div className={classnames('QuestionEdit__row', 'QuestionEdit__indented', 'QuestionEdit__hor-indented', 'QuestionEdit__checkboxContainer')}
                                        key={`${answer.id}_randomLine`}
                                    >
                                        <label>
                                            <input type="checkbox"
                                                name={answerRandomLineHtmlId}
                                                checked={!!answer.randomLine}
                                                onChange={(event) => this.updateAnswerProp(answer, 'randomLine', event.target.checked)}
                                                className="QuestionEdit__checkbox"
                                            />
                                            показывать произвольную строчку
                                        </label>
                                    </div>
                                    <div className={classnames('QuestionEdit__row', 'QuestionEdit__indented', 'QuestionEdit__hor-indented', 'QuestionEdit__checkboxContainer')}
                                        key={`${answer.id}_userInput`}
                                    >
                                        <label>
                                            <input type="checkbox"
                                                name={answerInputHtmlId}
                                                checked={!!answer.needUserInput}
                                                onChange={(event) => this.updateAnswerProp(answer, 'needUserInput', event.target.checked)}
                                                className="QuestionEdit__checkbox"
                                            />
                                            пользовательский ввод
                                        </label>
                                    </div>
                                    { answer.needUserInput && (
                                        <div className={classnames('QuestionEdit__row', 'QuestionEdit__hor-indented')} key={`${answer.id}_wrongInputGoto`}>
                                            <label htmlFor={answerWrongInputGotoHtmlId}>
                                                Ошибка на
                                            </label>
                                            <div className={'QuestionEdit__wideItem'}>
                                                <select id={answerWrongInputGotoHtmlId}
                                                    value={answer.incorrectUserInputNextQuestion || '0'}
                                                    name={answerWrongInputGotoHtmlId}
                                                    onChange={(event) => this.updateAnswerProp(answer, 'incorrectUserInputNextQuestion', parseInt(event.target.value, 10))}
                                                >
                                                    {!answer.incorrectUserInputNextQuestion && (
                                                        <option value="0">
                                                            Выберите точку перехода при неверном вводе
                                                        </option>
                                                    )}
                                                    {
                                                        qs.map(question => question && (
                                                            <option key={question.id} value={question.id}>
                                                                {/* {question.title || question.text} */}
                                                                {question.id } - { question.text }
                                                            </option>
                                                        ))
                                                    }
                                                </select>
                                                <div>
                                                    <a href={`#${this.getRootId(answer.incorrectUserInputNextQuestion)}`}>
                                                        {answer.incorrectUserInputNextQuestion ? 'к вопросу' : 'создать новый'}
                                                    </a>
                                                </div>
                                                {
                                                    !answer.incorrectUserInputNextQuestion && (
                                                        <div className="QuestionEdit__error">
                                                            Выберите точку перехода!!!
                                                        </div>
                                                    )
                                                }
                                            </div>
                                        </div>
                                    )}
                                    <div className={classnames('QuestionEdit__row', 'QuestionEdit__hor-indented')} key={`${answer.id}_goto`}>
                                        <label htmlFor={answerGotoHtmlId}>
                                            Перейти к
                                        </label>
                                        <div className={'QuestionEdit__wideItem'}>
                                            <select id={answerGotoHtmlId}
                                                value={answer.nextQuestion}
                                                name={answerGotoHtmlId}
                                                onChange={(event) => this.updateAnswerProp(answer, 'nextQuestion', parseInt(event.target.value, 10))}
                                            >
                                                {!answer.nextQuestion && (
                                                    <option value="0">
                                                        Выберите точку перехода!!!
                                                    </option>
                                                )}
                                                {
                                                    qs.map(question => question && (
                                                        <option key={question.id} value={question.id}>
                                                            {/* {question.title || question.text} */}
                                                            {question.id } - { question.text }
                                                        </option>
                                                    ))
                                                }
                                            </select>
                                            <div>
                                                <a href={`#${this.getRootId(answer.nextQuestion)}`}>
                                                    {answer.nextQuestion ? 'к вопросу' : 'создать новый'}
                                                </a>
                                            </div>
                                            {
                                                !answer.nextQuestion && (
                                                    <div className="QuestionEdit__error">
                                                        Выберите точку перехода!!!
                                                    </div>
                                                )
                                            }
                                        </div>
                                    </div>
                                </div>
                            );
                        })
                    }

                    <div className={'QuestionEdit__indented'}>
                        <button type="button" onClick={() => this.addAnswer()}>
                            Добавить ответ
                        </button>
                    </div>

                    <div className={'QuestionEdit__indented'}>
                        {isNew && (
                            <button type="submit" className={'QuestionEdit__submit'}>
                                Добавить этот вопрос
                            </button>
                        )}
                        {
                            this.props.onDelete && (
                                <button type="button" className={'QuestionEdit__delete'} onClick={() => this.props.onDelete!(question!)}>
                                    Удалить вопрос
                                </button>
                            )
                        }
                    </div>
                </form>
            </div>
        );
    }


    private updateQuestionProp<K extends keyof IQuestion>(propName: K, propValue: IQuestion[K]) {
        const question = { ...this.state.question, [propName]: propValue } as IQuestion;
        this.setState({
            question,
            hideTextError: false
        });

        if (!this.state.isNew) {
            this.onSubmit(question);
        }
    }


    private updateAnswerProp<K extends keyof IAnswer>(originalAnswer: IAnswer, propName: K, propValue: IAnswer[K]) {
        const updatedAnswer = { ...originalAnswer, [propName]: propValue } as IAnswer;
        const answers = this.state.question!.answers.map(answer => answer && answer.id === originalAnswer.id ? updatedAnswer : answer);
        this.updateQuestionProp('answers', answers);
    }


    private onSubmit(question: IQuestion, event?: React.BaseSyntheticEvent) {
        if (event) {
            event.preventDefault();
        }

        if (!question.text) {
            return;
        }

        if (this.state.isNew) {
            if (event) {
                this.props.onSave(question);
                this.setState({
                    hideTextError: true,
                    question: getNewQuestion()
                });
            }
        } else {
            this.props.onSave(question);
        }
    }


    private deleteAnswer(answerIndex: number) {
        const answers = this.state.question!.answers.map((answer, index) => !answer || index === answerIndex ? null : answer);
        this.updateQuestionProp('answers', answers);
    }


    private addAnswer() {
        const stateAnswers = this.state.question!.answers;
        const newAnswer: IAnswer = {
            id: stateAnswers.length + 1,
            text: '',
            nextQuestion: 0,
        };
        const answers = [...stateAnswers, newAnswer];
        this.updateQuestionProp('answers', answers);
    }


    private getRootId(questionId?: number): string {
        const baseId = 'QuestionEdit_root_';
        return questionId ? `${baseId}_${questionId}` : `${baseId}_new`;
    }

}
