import { IUser } from '../models/user';

export type IPlaceholders = '<%reg%>' | '<%login%>' | '<%br%>';

export const placeholdersInfo: { [key in IPlaceholders]: string} = {
    '<%reg%>': 'поля для ввода регистрационных данных (только для вопросов)',
    '<%login%>': 'вывод логина или "Пользователь" (по умолчанию)',
    '<%br%>': 'принудительный перенос строки (удобно для рандомов)'
};


export const placeholders: {
    reg: IPlaceholders,
    login: IPlaceholders,
    br: IPlaceholders,
} = {
    reg: '<%reg%>',
    login: '<%login%>',
    br: '<%br%>',
};


function handleBr(text: string): string {
    return text.split(placeholders.br).join('\n');
}


function handleLogin(text: string, user?: IUser | null): string {
    const login = user ? user.login : 'Пользователь';
    return text.split(placeholders.login).join(login);
}


export function handlePlaceholders(text: string, user?: IUser | null) {
    const afterBr = handleBr(text);
    const afterLogin = handleLogin(afterBr, user);
    return afterLogin;
}
