import { IQuestionsList } from '../models/qa';
import { IUser } from '../models/user';


class StorageValue<T> {

    constructor(private key = '') {
    }


    store(value: T) {
        const serializedValue = JSON.stringify(value);
        window.localStorage.setItem(this.key, serializedValue);
    }


    restore(defaultValue?: T): T | null {
        const defaultValueToUse = defaultValue || null;
        const serializedValue = window.localStorage.getItem(this.key);
        if (!serializedValue) {
            return defaultValueToUse;
        }
        try {
            return JSON.parse(serializedValue) as T;
        } catch (e) {
            console.error('serialized value', serializedValue);
            console.error(`while parsing serialized value from key ${this.key}`, e);
        }
        return defaultValueToUse;
    }
}


export const storage = {
    questions: new StorageValue<IQuestionsList>('mobius2019_questions'),
    users: new StorageValue<IUser[]>('mobius2019_users'),
    adminPass: new StorageValue<string>('mobius2019_adminPass'),
};
